/* src/app/components/watchlist/watchlist.scss */
.watchlist-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--sys-surface-container-high, #181a20);
  color: var(--sys-on-surface, #ddd);
  padding-bottom: calc(var(--footer-height));
}
.scrollable-list {
  flex: 1;
  overflow-y: auto;
  padding: 8px 12px 12px;
  box-sizing: border-box;
  scrollbar-width: thin;
}
.group-label {
  margin: 18px 0 6px;
  font-weight: 600;
  font-size: 13px;
  letter-spacing: 0.5px;
  color: var(--sys-on-surface-variant, #8a8f99);
  text-transform: uppercase;
  position: sticky;
  top: 0;
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  padding: 4px 0;
}
.watchlist-row-card {
  display: flex;
  flex-direction: column;
  padding: 10px 14px 8px;
  margin: 0 0 10px;
  background: var(--sys-surface-container, #20242b);
  border: 1px solid #262a30;
  border-radius: 12px;
  cursor: pointer;
  position: relative;
  transition:
    background 0.2s ease,
    transform 0.16s ease,
    box-shadow 0.16s ease;
  overflow: hidden;
}
.watchlist-row-card::before {
  content: "";
  position: absolute;
  inset: 0;
  background:
    linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.04),
      rgba(255, 255, 255, 0));
  opacity: 0;
  transition: opacity 0.25s ease;
  pointer-events: none;
}
.watchlist-row-card:hover {
  background: var(--sys-surface-container-high, #181a20);
  transform: translateY(-2px);
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.35);
}
.watchlist-row-card:hover::before {
  opacity: 1;
}
.watchlist-row-card:active {
  transform: translateY(0);
}
.row-top {
  display: flex;
  justify-content: space-between;
  font-weight: 600;
  font-size: 13px;
  color: var(--sys-on-surface, #eee);
}
.row-top .timeframe {
  font-weight: 500;
  color: #8a8f99;
}
.row-bottom {
  margin-top: 4px;
  display: flex;
  align-items: center;
  gap: 8px;
}
.row-bottom .chip-list {
  display: flex;
  gap: 4px;
}
.row-bottom .chip-list mat-chip {
  height: 22px;
  font-size: 12px;
  padding: 0 6px;
}
.row-bottom .chip-list .bear,
.row-bottom .chip-list .short {
  background: #e57373;
  color: white;
}
.row-bottom .chip-list .bull,
.row-bottom .chip-list .long {
  background: #81c784;
  color: white;
}
.row-bottom .chip-list .divergence {
  background: #64b5f6;
  color: white;
}
.row-bottom .chip-list .none {
  background: #bdbdbd;
  color: white;
}
.row-bottom .created {
  font-size: 11px;
  color: #8a8f99;
  margin-left: auto;
  font-variant-numeric: tabular-nums;
}
.header-actions {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 8px 8px 0;
  height: 54px;
  position: sticky;
  top: 0;
  z-index: 10;
  background: var(--sys-surface-container-high, #181a20);
  border-bottom: 1px solid var(--sys-outline, #222);
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  padding: 8px 4px;
}
.header-actions button[mat-button] {
  height: 34px;
  line-height: 34px;
  font-size: 13px;
  font-weight: 500;
  color: var(--sys-on-surface, #eee);
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid #2a2d33;
  border-radius: 8px;
  padding: 0 14px;
  transition:
    background 0.2s ease,
    transform 0.2s ease,
    box-shadow 0.2s ease;
  text-transform: capitalize;
}
.header-actions button[mat-button]:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-1px);
}
.header-actions button[mat-button]:active {
  background: rgba(255, 255, 255, 0.12);
  transform: translateY(0);
}
.header-actions .spacer {
  flex: 1 1 auto;
}
.header-actions .monitoring-select.mat-mdc-form-field.dense {
  max-width: 230px;
  height: 65px;
}
.header-actions .monitoring-select.mat-mdc-form-field.dense .mat-mdc-form-field-flex {
  min-height: 34px;
  height: 34px;
  padding: 0 10px !important;
}
.header-actions .monitoring-select.mat-mdc-form-field.dense .mat-mdc-form-field-infix {
  padding: 0 !important;
  min-height: 34px !important;
}
.header-actions .monitoring-select.mat-mdc-form-field.dense .mat-mdc-select-trigger {
  height: 34px;
  display: flex;
  align-items: center;
}
.header-actions .monitoring-select.mat-mdc-form-field.dense .mat-mdc-select-value,
.header-actions .monitoring-select.mat-mdc-form-field.dense .mat-mdc-select-placeholder {
  font-size: 13px;
}
.header-actions .monitoring-select.mat-mdc-form-field.dense .mat-mdc-form-field-outline-start,
.header-actions .monitoring-select.mat-mdc-form-field.dense .mat-mdc-form-field-outline-end {
  height: 34px;
}
.chip-list mat-chip {
  font-weight: 500;
  letter-spacing: 0.3px;
  border-radius: 6px !important;
}
.chip-list mat-chip.bear,
.chip-list mat-chip.short {
  background: #b04141;
}
.chip-list mat-chip.bull,
.chip-list mat-chip.long {
  background: #2e7d32;
}
.chip-list mat-chip.divergence {
  background: #1565c0;
}
.chip-list mat-chip.none {
  background: #616161;
}
@media (max-width: 600px) {
  .watchlist-row-card {
    padding: 8px 10px 6px;
    border-radius: 10px;
  }
  .row-top {
    font-size: 12px;
  }
  .header-actions {
    height: 50px;
  }
  .group-label {
    font-size: 12px;
  }
}
